/*
 * Enable fancybox overlay popup
 */
(function ($) {
	"use strict";

	$("[data-fancybox]").fancybox({
		toolbar  : false,
		smallBtn : true,
		iframe : {
			preload : false
		}
	});

}(jQuery));

/*
 * Redirect to support form
 */
(function ($) {
	"use strict";

	MktoForms2.whenReady(function () {
		$(".Call_Request_Options__c").on("change", function (event) {
			var selectedOption = $(event.currentTarget).find(":selected").val();
			if (selectedOption === "Technical support") {
				window.location.href = "/forms/contact-support/";
				$("select,textarea,input,.mktoButton").prop('disabled', true).addClass("disabled");
				$('.mktoForm button[type="submit"]').text("");
			}
		});
	});

}(jQuery, MktoForms2));